<template>
  <div class="game">
    <section class="game__nav">
      <img v-for="(char, k) in gameTeam" :src='`/img/game/${char.facepic}.jpg`' :key='k'>
      <input
        type="text"
        v-model="user"
        placeholder="username"
      >
      <button @click="startGame">Start game</button>
    </section>

    <section class="game__main">
      <CharCollection class="game__collection"/>
    </section>
  </div>
</template>

<script>
import CharCollection from "@/components/CharCollection.vue"
export default {
  components:{
    CharCollection
  },
  sockets: {
    yourCharacters: function(roster) {
      this.$store.commit("roster/SET_CHARACTERS", roster);
    },
    charError: function(err) {
      alert(err);
    },
  },
  data() {
    return {
      user: ""
    };
  },
  computed:{
    gameTeam(){
      return this.$store.getters['roster/getGameTeam']
    }
  },
  methods: {
    startGame() {
      const user = {
        id: this.user,
        characters: this.$store.getters['roster/getGameTeam']
      }
      this.$socket.client.emit("startGame", user);
    }
  },
  created() {
    if (!this.$store.getters["roster/getCharacters"].length) {
      this.$socket.client.emit("myCharacters");
    }
  }
};
</script>
