const SET_SELECTION = function(state, payload){
    state.skill = payload.skill
    state.caster = payload.caster
    state.position = payload.position
    state.skillIndex = payload.skillIndex
    state.skillPreview = payload.skill
    if(payload.isAlly){
        state.targetAllyStatus = true
    } else {
        state.targetEnemyStatus = true
    }
}

const RESET_STATE = function(state, payload){
    state.caster = ''
    state.position = -1
    state.skill = {}
    state.skillIndex = -1
    
    if (payload.all){
        state.targetAllyStatus = false
        state.targetEnemeyStatus = false
        return
    }
    
    if(payload.isAlly){
        state.targetAllyStatus = false
    } else {
        state.targetEnemyStatus = false
    }
}

const SET_SKILL_PREVIEW = function(state, skill){
    state.skillPreview = skill
}

export default {
    SET_SELECTION,
    RESET_STATE,
    SET_SKILL_PREVIEW
}