const getMyFriends = function(state){
    return state.friends
}

const getOneFriendByIndex = function(state){
    return index => {
        return state.friends[index]
    }
}

const getMyFoes = function(state){
    return state.foes
} 
const getMyTurn = function(state){
    return state.myturn
}

const getMyOpponent = function(state){
    return state.opponent
}

const getActiveEffects = function(state){
    return (x, team) => {
        return state[team][x].activeEffects
    }
}


const getSkillQueue = function(state){
    return state.skillQueue
}

const getSkillQueueFilter = function(state){
    return id => {
        return state.skillQueue.filter(target => {
            for(const i in target.targets){
                if(target.targets[i].target.includes(id)) return true
            }
        })
    }
}

export default {
    getMyFoes,
    getMyFriends,
    getOneFriendByIndex,
    getMyTurn,
    getMyOpponent,
    getActiveEffects,
    getSkillQueue,
    getSkillQueueFilter
}