<template>
  <section>
    <div
      class="game__char"
      v-for="(char, key) in roster"
      :key="key"
      @click="selectThisCharacter(char)"
    > 
      <img class="game__char__icon1" :src="`/img/game/${char.facepic}.jpg`">
      <img v-show="pop==char.facepic" class="game__char__icon2" :src="`/img/game/${char.facepic}.jpg`">
    </div>
  </section>
</template>

<script>
export default {
  computed: {
    roster() {
      return this.$store.getters["roster/getCharacters"];
    }
  },
  data(){
    return {
      pop: ''
    }
  },
  methods:{
    selectThisCharacter(char){
      this.$store.commit("roster/ADD__CHARACTER", char)
      this.pop = char.facepic
    }
  }
};
</script>