const SET_GAME = function(state, payload){
    state.opponent = payload.opponent
    state.friends = payload.friends
    state.foes = payload.foes
    state.myturn = payload.myturn
}

const SET_TARGET_ENEMY_STATUS = function(state, status){
    state.targetEnemyStatus = status
}

const SET_TARGET_ALLY_STATUS = function(state, status){
    state.targetAllyStatus = status
}

const ADD_ENEMY_EFFECT_LIST = function(state, payload){
    state.foes[payload.index].effectList.push(payload.effect) 
}

const ADD_TO_SKILL_QUEUE = function(state, skill){
    state.skillQueue.push(skill)
}

const REMOVE_FROM_SKILL_QUEUE = function(state, index){
    state.skillQueue.splice(index, 1)
}

const RESET_SKILL_QUEUE = function(state){
    state.skillQueue=[]
}

export default {
    SET_GAME,
    SET_TARGET_ENEMY_STATUS,
    SET_TARGET_ALLY_STATUS,
    ADD_ENEMY_EFFECT_LIST,
    ADD_TO_SKILL_QUEUE,
    REMOVE_FROM_SKILL_QUEUE,
    RESET_SKILL_QUEUE
}