const getSelectedSkill = function(state){
    return state.skill
}
const getTargetEnemyStatus = function(state){
    return state.targetEnemyStatus
}

const getTargetAllyStatus = function(state){
    return state.targetAllyStatus
}

const getSelectedSkillInfo = function(state){
    const s = {
        caster: state.caster,
        position: state.position,
        skill: state.skill,
        skillIndex: state.skillIndex
    }
    return s 
}

const getSkillPreview = function(state){
    return state.skillPreview
}

export default {
    getSelectedSkill,
    getTargetAllyStatus,
    getTargetEnemyStatus,
    getSelectedSkillInfo,
    getSkillPreview
}